/* eslint-disable @typescript-eslint/no-unused-vars */
import { HttpRequest, configRequest } from 'utils/request';
import { AuthApiRoutes } from '../constants/auth.constant';
import { APP_CONSTANT } from 'store/common/constants/common.constant';
import * as _ from 'lodash';
export class AuthHttp {
  request: any;
  endPoint: any;
  constructor(endPoint = APP_CONSTANT.API.USER) {
    this.request = new HttpRequest(endPoint).request;
    this.endPoint = endPoint;
  }

  login = (loginInfo: any): Promise<any> => {
    return this.request.post(AuthApiRoutes.LOGIN, loginInfo);
  };

  getListUser = (filter: any): Promise<any> => {
    return this.request.get(AuthApiRoutes.USER, configRequest(filter));
  };

  getUserByIdToken = (userId: string): Promise<any> => {
    return this.request.get(AuthApiRoutes.USER + '/' + userId);
  };

  getUserById = (id: string): Promise<any> => {
    return this.request.get(AuthApiRoutes.USER + '/' + id);
  };

  addRole = (role: any): Promise<any> => {
    return this.request.post(AuthApiRoutes.ROLE, role);
  };

  deleteRole = (id: string): Promise<any> => {
    return this.request.delete(AuthApiRoutes.ROLE + '/' + id);
  };

  getListRole = (filter: any): Promise<any> => {
    return this.request.get(AuthApiRoutes.ROLE, configRequest(filter));
  };

  getRoleById = (id: string): Promise<any> => {
    return this.request.get(AuthApiRoutes.ROLE + '/' + id);
  };

  updatePermissionInRole = ({ roleId, permission_ids }): Promise<any> => {
    return this.request.put(
      AuthApiRoutes.UPDATE_PERMISSION_IN_ROLE + '/' + roleId,
      {
        permission_ids,
      },
    );
  };

  updateUsersInRole = ({ id, activeUser, inActiveUser }: any): Promise<any> => {
    return this.request.put(AuthApiRoutes.ROLE + '/users/' + id, {
      active_user_ids: activeUser,
      inactive_user_ids: inActiveUser,
    });
  };

  getListPermission = (): Promise<any> => {
    return this.request.get(AuthApiRoutes.GET_LIST_PERMISSION);
  };

  createPersonnel = (personnelInfo: any): Promise<any> => {
    return this.request.post(AuthApiRoutes.CREATE_PERSONNEL, {
      ...configRequest(personnelInfo),
    });
  };

  updatePersonnel = (personal): Promise<any> => {
    return this.request.put(AuthApiRoutes.USER + '?id=' + personal.id, {
      ...configRequest(personal),
    });
  };

  checkUserNamePersonnel = (userInfo): Promise<any> => {
    return this.request.post(AuthApiRoutes.CHECK_USER_NAME_PERSONNEL, {
      ...configRequest(userInfo),
    });
  };

  checkCodePersonnel = (userInfo): Promise<any> => {
    return this.request.post(AuthApiRoutes.CHECK_CODE_PERSONNEL, {
      ...configRequest(userInfo),
    });
  };

  deletePersonnel = (id: string): Promise<any> => {
    return this.request.delete(AuthApiRoutes.USER + '/' + id);
  };

  changeActivePersonnel = ({ id, isActive }): Promise<any> => {
    return this.request.post(AuthApiRoutes.CHANGE_ACTIVE, {
      Id: id,
      is_active: isActive,
    });
  };

  checkPassWord = ({ userName, passWord }): Promise<any> => {
    return this.request.post(AuthApiRoutes.CHECK_PASSWORD, {
      Password: passWord,
      User_Name: userName,
    });
  };

  updateProfileUser = (user: any): Promise<any> => {
    return this.request.put(
      AuthApiRoutes.UPDATE_PROFILE_USER + '/' + user.id,
      user,
    );
  };
}
