/* eslint-disable @typescript-eslint/no-unused-vars */
import { createSlice } from 'store/core/@reduxjs/toolkit';
import {
  RESPONSE_CONSTANT,
  RESPONSE_MESSAGE,
} from '../constants/auth.constant';
import { AppHelper } from 'utils/app.helper';

export interface AuthState {
  isAuthenticated: boolean;
  loading: boolean;
  error: any;
  success: any;
  user: any;
  users: any;
  roles: any;
  token: string;
  permissions: any;
  isEditingRole: any;
  userFind: any;
  titles: any;
  positions: any;
}

export const initialState: AuthState = {
  isAuthenticated: false,
  loading: false,
  error: null,
  success: {},
  user: {},
  token: '',
  users: {},
  roles: {},
  permissions: {},
  isEditingRole: {},
  userFind: {},
  titles: {},
  positions: {},
};

const AuthSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    login(state, action) {
      state.loading = true;
    },

    loginSuccess(state, action) {
      state.isAuthenticated = true;
      state.loading = false;
      state.user = action.payload;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.LOGIN_SUCCESS,
        message: RESPONSE_MESSAGE.LOGIN_SUCCESS,
      };
      state.token = action.payload.token;
    },

    loginFail(state, action) {
      state.loading = false;
      state.user = {};
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.LOGIN_FAIL,
        message: action.payload,
      };
      state.token = '';
      state.isAuthenticated = false;
    },

    getUserByIdToken(state, action) {
      state.loading = true;
    },
    getUserByIdTokenSuccess(state, action) {
      state.isAuthenticated = true;
      state.loading = false;
      state.user = action.payload;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.GET_USER_BY_ID_SUCCESS,
        message: RESPONSE_MESSAGE.GET_USER_BY_ID_SUCCESS,
      };
      state.token = action.payload.token;
    },
    getUserByIdTokenFail(state, action) {
      state.loading = false;
      state.user = {};
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.GET_USER_BY_ID_FAIL,
        message: action.payload,
      };
      state.token = '';
      state.isAuthenticated = false;
    },

    getUserById(state, action) {
      state.loading = true;
    },

    getUserByIdSuccess(state, action) {
      state.loading = false;
      state.userFind = action.payload;
    },

    getUserByIdFail(state, action) {
      state.loading = false;
      state.userFind = {};
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.GET_USER_BY_ID_FAIL,
        message: action.payload,
      };
    },

    getListUser(state, action) {
      state.loading = true;
    },

    getListUserSuccess(state, action) {
      state.loading = false;
      state.users = action.payload;
    },

    getListUserFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getListRole(state, action) {
      state.loading = true;
    },

    getListRoleSuccess(state, action) {
      state.loading = false;
      state.roles = action.payload;
    },

    getListRoleFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getRoleById(state, action) {
      state.loading = true;
    },

    getRoleByIdSuccess(state, action) {
      const { data } = action.payload;
      state.loading = false;
      state.isEditingRole = data;
    },

    getRoleByIdFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    setUserTitles(state, action) {
      state.titles = action.payload;
    },

    setUserPositions(state, action) {
      state.positions = action.payload;
    },

    addRole(state, action) {
      state.loading = true;
    },

    addRoleSuccess(state, action) {
      state.loading = false;
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.ADD_ROLE_SUCCESS,
        message: RESPONSE_MESSAGE.ADD_ROLE_SUCCESS,
      };
    },

    addRoleFail(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.UPDATE_ROLE_FAIL,
        message: action.payload,
      };
    },

    deleteRole(state, action) {
      state.loading = true;
    },

    deleteRoleSuccess(state, action) {
      state.loading = false;
    },

    deleteRoleFail(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.DELETE_ROLE_FAIL,
        message: RESPONSE_CONSTANT.DELETE_ROLE_FAIL,
      };
    },

    updatePermissionInRole(state, action) {
      state.loading = true;
    },

    updatePermissionInRoleSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.UPDATE_ROLE_SUCCESS,
        message: RESPONSE_MESSAGE.UPDATE_ROLE_SUCCESS,
      };
    },

    updatePermissionInRoleFail(state, action) {
      state.loading = false;
    },

    addUserInRole(state, action) {
      state.loading = true;
    },

    addUserInRoleSuccess(state, action) {
      state.loading = false;
      state.isEditingRole = {
        ...state.isEditingRole,
        users: action.payload.users.length
          ? action.payload.users.filter(user => user)
          : [],
      };
    },

    addUserInRoleFail(state, action) {
      state.loading = false;
    },

    deleteUserInRole(state, action) {
      state.loading = true;
    },

    deleteUserInRoleSuccess(state, action) {
      state.loading = false;
      state.isEditingRole = {
        ...state.isEditingRole,
        users: action.payload.users.length
          ? action.payload.users.filter(user => user)
          : [],
      };
    },

    deleteUserInRoleFail(state, action) {
      state.loading = false;
    },

    getListPermission(state) {
      state.loading = true;
    },

    getListPermissionSuccess(state, action) {
      state.loading = false;
      state.permissions = action.payload;
    },

    getListPermissionFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    logout(state) {
      state.loading = false;
      state.user = {};
      state.token = '';
      state.isAuthenticated = false;
    },

    deletePersonnel(state, action: any) {
      state.loading = true;
      state.error = null;
    },

    deletePersonnelSuccess(state, action: any) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.DELETE_PERSONNEL_SUCCESS,
        message: RESPONSE_MESSAGE.DELETE_MESSAGE_SUCCESS,
      };
    },

    deletePersonnelFail(state, action: any) {
      state.loading = false;
      state.error = action.payload;
    },

    changeActivePersonnel(state, action: any) {
      state.loading = true;
      state.error = null;
    },

    changeActivePersonnelSuccess(state, action: any) {
      const oldUsers: any = AppHelper.getValueFromProxy(state.users);
      const { id, isActive } = action.payload;
      state.loading = false;
      state.users = {
        ...oldUsers,
        data: oldUsers.data.map(user => {
          if (user.id !== id) return user;
          return {
            ...user,
            is_active: isActive,
          };
        }),
      };
    },

    changeActivePersonnelFail(state, action: any) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.CHANGE_ACTIVE_FAIL,
        message: RESPONSE_MESSAGE.CHANGE_ACTIVE_MESSAGE_FAIL,
      };
    },

    createPersonnel(state, action: any) {
      state.loading = true;
      state.error = null;
    },

    createPersonnelSuccess(state, action: any) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.CREATE_PERSONNEL_SUCCESS,
        message: RESPONSE_MESSAGE.CREATE_PERSONNEL_SUCCESS,
      };
    },

    createPersonnelFail(state, action: any) {
      state.loading = false;
      state.error = action.payload;
    },

    updatePersonnel(state, action: any) {
      state.loading = true;
      state.error = null;
    },

    updatePersonnelSuccess(state, action: any) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.UPDATE_PERSONNEL_SUCCESS,
        message: RESPONSE_MESSAGE.UPDATE_PERSONNEL_SUCCESS,
      };
    },

    updatePersonnelFail(state, action: any) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.UPDATE_PERSONNEL_FAIL,
        message: RESPONSE_MESSAGE.UPDATE_PERSONNEL_FAIL,
      };
    },

    updateProfileUser(state, action: any) {
      state.loading = true;
      state.error = null;
    },

    updateProfileUserSuccess(state, action: any) {
      state.isAuthenticated = true;
      state.loading = false;
      state.user = action.payload;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.UPDATE_PROFILE_USER_SUCCESS,
        message: RESPONSE_MESSAGE.UPDATE_PROFILE_USER_SUCCESS,
      };
      state.token = action.payload.token;
    },

    updateProfileUserFail(state, action: any) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.UPDATE_PROFILE_USER_FAIL,
        message: RESPONSE_MESSAGE.UPDATE_PROFILE_USER_FAIL,
      };
    },

    checkPassWord(state, action: any) {
      state.loading = true;
      state.error = null;
    },

    checkPassWordSuccess(state, action: any) {
      state.loading = false;
    },

    checkPassWordFail(state, action: any) {
      state.loading = false;
      state.error = action.payload;
    },

    clearEditRole(state) {
      state.isEditingRole = {};
    },

    clearSuccess(state) {
      state.success = {};
    },

    clearError(state) {
      state.error = {};
    },

    setError(state, action) {
      state.error = action.payload;
    },

    setSuccess(state, action) {
      state.success = action.payload;
    },

    clearUsers(state) {
      state.users = {};
    },

    clearData(state) {
      state.userFind = {};
    },
  },
});

export const { actions, reducer, name: sliceKey } = AuthSlice;
