export function* getRoleById(api, action) {
  const { type, id } = action.payload;
  const response = yield call(api.getRoleById, id);
  try {
    const data = yield configResponse(response);
    yield put(
      actions.getRoleByIdSuccess({
        data: {
          ...data,
          users: data.users.length ? data.users.filter(user => user) : [],
        },
        type,
      }),
    );
  } catch (error) {
    yield put(actions.getRoleByIdFail(_.get(error, 'message')));
  }
}

