export function* updateProfileUser(api, action) {
  const response = yield call(api.updateProfileUser, action.payload);
  try {
    const data = yield configResponse(response);
    // const userInfo = yield SagaHelper.addUserRoute(data);
    delete data.permissions;
    delete data.role;
    const userInfo = {
      ...(yield select(selectUser)),
      ...data,
    };
    yield put(actions.updateProfileUserSuccess(userInfo));
  } catch (error) {
    yield put(actions.updateProfileUserFail(_.get(error, 'message')));
  }
}

export function* AuthSaga() {
  yield all([
    yield takeLatest(actions.getUserById.type, getUserById, userHttp),
    yield takeLatest(actions.getUserByIdToken.type, getUserByIdToken, userHttp),
    yield takeLatest(actions.login.type, login, authRequest),
    yield takeLatest(actions.getListUser.type, getListUser, userHttp),
    yield takeLatest(actions.getListRole.type, getListRole, userHttp),
    yield takeLatest(actions.getRoleById.type, getRoleById, userHttp),
    yield takeLatest(actions.deleteRole.type, deleteRole, userHttp),
    yield takeLatest(actions.addRole.type, addRole, userHttp),
    yield takeLatest(
      actions.updatePermissionInRole.type,
      updatePermissionInRole,
      userHttp,
    ),
    yield takeLatest(actions.addUserInRole.type, addUserInRole, userHttp),
    yield takeLatest(actions.deleteUserInRole.type, deleteUserInRole, userHttp),
    yield takeLatest(
      actions.getListPermission.type,
      getListPermission,
      userHttp,
    ),
    yield takeLatest(actions.deletePersonnel.type, deletePersonnel, userHttp),
    yield takeLatest(
      actions.changeActivePersonnel.type,
      changeActivePersonnel,
      userHttp,
    ),
    yield takeLatest(actions.createPersonnel.type, createPersonnel, userHttp),
    yield takeLatest(actions.updatePersonnel.type, updatePersonnel, userHttp),
    yield takeLatest(actions.checkPassWord.type, checkPassWord, userHttp),
    yield takeLatest(
      actions.updateProfileUser.type,
      updateProfileUser,
      userHttp,
    ),
  ]);
}

class SagaHelper {
  static addUserRoute(user: any) {
    try {
      const authService = new PrivateAuthRoute(user);
      const userRoutes = authService.seperateRoute(AppRouting);
      const userInfo: any = {
        ...user,
        userRoutes,
      };
      return userInfo;
    } catch (err) {
      throw new Error(err as any);
    }
  }
}
